import React from "react";
import "./main.scss";
import Slider from "infinite-react-carousel";

export default function Main({ menuOpen, setMenuOpen }) {
  const settings = {
    arrows: false,
    arrowsBlock: false,
    autoplay: true,
    autoplaySpeed: 1800,
    dots: true,
    dotsScroll: 1,
    pauseOnHover: false,
    swipe: true,
    shift: 30,
  };
  return (
    <div className="Main" id="main">
      <div className="container">
        <div className="container1">
          <h1>Introduction</h1>
          <p>
            The Saadian Tombs are a historic royal necropolis in Marrakesh,
            Morocco, located on the south side of the Kasbah Mosque, inside the
            royal kasbah (citadel) district of the city. They date to the time
            of the Saadian dynasty and in particular to the reign of Ahmad
            al-Mansur (15781603), though members of Morocco's monarchy
            continued to be buried here for a time afterwards. The complex is
            regarded by many art historians as the high point of Moroccan
            architecture in the Saadian period due to its luxurious decoration
            and careful interior design. Today the site is a major tourist
            attraction in Marrakesh
          </p>
          <h1>About</h1>
          <p>
            The Saadian Tombs are located just outside Marrakech and were
            constructed during the reign of Sultan Ahmad al-Mansur in the late
            16th century. This is the resting place of al-Mansur, his family,
            and other notable officials from that era. These tombs were
            discovered in 1917; restoration began shortly thereafter. Today,
            much of its original splendour has been restored.
          </p>

          <h1>Tips for visiting</h1>
          <p>
            <li>
              site is busy with tour groups from about 9:30am to 1pm, and a long
              line can form to view Al Mansours chamber. Either get here right
              at opening time to admire the tombs in peace or come later in the
              day.
            </li>
            <li>
              Late afternoon is the best time for photography as the marble work
              takes on a golden hue in the light.
            </li>{" "}
          </p>
        </div>
        <div className="container2">
          <Slider {...settings} className="Slider">
            <div>
              <img
                src={require("./21.jpg")}
                alt="1"
                height="550px"
                width="450px"
              />
            </div>
            <div>
              <img
                src={require("./22.jpg")}
                alt="2"
                height="550px"
                width="450px"
              />
            </div>
            <div>
              <img
                src={require("./23.jpg")}
                alt="3"
                height="550px"
                width="450px"
              />
            </div>
            <div>
              <img
                src={require("./24.jpg")}
                alt="4"
                height="550px"
                width="450px"
              />
            </div>
            <div>
              <img
                src={require("./25.jpg")}
                alt="5"
                height="550px"
                width="450px"
              />
            </div>
            <div>
              <img
                src={require("./26.jpg")}
                alt="6"
                height="550px"
                width="450px"
              />
            </div>
            <div>
              <img
                src={require("./27.jpg")}
                alt="7"
                height="550px"
                width="450px"
              />
            </div>
          </Slider>
        </div>
      </div>
    </div>
  );
}
